@import url(https://fonts.googleapis.com/css2?family=Cinzel+Decorative&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Barlow&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Barlow&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.recipe {
  background-image: linear-gradient(-225deg, #e3fdf5 0%, #ffe6fa 100%);
  margin: 30px 10px;
  border-radius: 5%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  box-shadow: 0px 5px 10px rgb(68, 65, 65);
  border-radius: 10px;
  min-width: 40%;
  padding: 15px;
  font-family: "Barlow", "Cinzel Decorative";
}
h1 {
  font-family: "Cinzel Decorative";
}

.image {
  height: 30%;
  width: 50%;
  border-radius: 50%;
  padding: 5px;
}

.Link {
  display: flex;
  background-color: lightcoral;
  color: black;
  border-radius: 3px;
  margin-top: 12px;
  margin-bottom: 12px;
  cursor: pointer;
  box-shadow: 0px 2px 2px rgb(100, 96, 96);
}

.Link:hover {
  background-image: linear-gradient(to top, #ff0844 0%, #ffb199 100%);
  transform: scale(1.05);
  box-shadow: 0px 10px 10px rgb(100, 96, 96);
}

.icon-link {
  margin-left: 0.75rem;
  font-size: 1.2rem;
}

a {
  margin: 0 auto;
  color: black;
  font-weight: bolder;
  font-family: "Courier New", Courier, monospace;
  text-decoration: none;
}

.App {
  min-height: 100vh;
  background-color: lightsteelblue;
}

.shopName {
  margin-top: 0px;
  text-align: center;
  background: white;
  color: black;
  padding: 15px;
  font-family: "Pacifico", "Roboto", "Courier New", Courier, monospace;
  border: 30px solid thistle;
  text-decoration: underline;
}

.image {
  height: 80px;
  width: 80px;
}

.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar {
  background-color: rgb(247, 247, 248);
  border: 0;
  width: 50%;
  height: 30px;
  padding: 0 20px;
  margin: 20px;
  border-radius: 10px;
  font-weight: bolder;
}

.search-bar::-webkit-input-placeholder {
  color: rgba(153, 141, 141, 0.5);
}

.search-bar:-ms-input-placeholder {
  color: rgba(153, 141, 141, 0.5);
}

.search-bar::-ms-input-placeholder {
  color: rgba(153, 141, 141, 0.5);
}

.search-bar::placeholder {
  color: rgba(153, 141, 141, 0.5);
}

.search-bar:focus,
.search-button:focus {
  outline: none;
}

.search-button {
  height: 30px;
  width: 85px;
  border-radius: 5px;
  border: 0;
  background-color: darkorange;
  cursor: pointer;
  font-weight: bolder;
  font-size: 15px;
}

.search-button:hover {
  background-color: orange;
  transform: scale(1.02);
  box-shadow: 0px 5px 10px rgb(68, 65, 65);
}

.icon-search {
  margin-left: 0.1rem;
  font-size: 1.25rem;
}

.message-search {
  text-align: center;
  text-transform: capitalize;
  color: white;
  background-color: rgba(62, 145, 106, 0.5);
  margin-left: 20%;
  margin-right: 18%;
  margin-top: 4%;
  margin-bottom: 4%;
  font-family: "Barlow";
}

.message-end {
  text-align: center;
  text-transform: capitalize;
  padding-bottom: 50px;
  padding-top: 10px;
  color: rgba(54, 51, 51, 0.5);
}

.recipes {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

